<template>
  <div>
    <van-nav-bar title="订单详情" left-arrow left-text="返回" @click-left="$router.back()"></van-nav-bar>
    <div class="list">
      <van-steps :active="steps">
        <van-step>未付款</van-step>
        <van-step>已付款</van-step>
        <van-step>已发货</van-step>
        <van-step>交易完成</van-step>
      </van-steps>
      <van-cell-group class="item-gap">
        <van-cell :title="address.title" :label="address.label" />
      </van-cell-group>
      <van-cell-group class="item-gap">
        <van-cell v-for="product in orderProducts" :key="product.id" :title="product.name" :value="`${product.count}件`"
          :label="`￥${product.price}`" :icon="product.cover" />
      </van-cell-group>
    </div>
    <van-popup v-model="showBottomBar" class="bottom-bar" position="bottom" :overlay="false">
      <div class="total">
        合计：<span>{{total}}</span>
      </div>
      <van-button type="primary" v-if="order.order_status === 0" @click="showPayPassword = true">付款</van-button>
      <van-tag class="text" size="large" v-if="order.order_status === 1">等待商家发货</van-tag>
      <!-- <div class="text" v-if="order.order_status === 1">等待商家发货</div> -->
    </van-popup>
    <van-popup class="van-popup-pay" v-model="showPayPassword" position="bottom" :close-on-click-overlay="false">
      <van-nav-bar class="pay-nav-bar" title="付款" @click-left="showPayPassword = false">
        <van-icon slot="left" name="cross" />
      </van-nav-bar>
      <van-password-input :value="payPassword" info="密码为 6 位数字" />
      <van-number-keyboard :show="showPayPassword" @input="onInput" @delete="onDelete" />
    </van-popup>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        showPayPassword: false,
        payPassword: '',
        showBottomBar: true,
        order: {}
      }
    },
    computed: {
      steps(){
        if(this.order.order_status >= 0){
          return this.order.order_status
        }else{
          return -1
        }
      },
      address() {
        return this.order.userAddress ? {
          title: `${this.order.userAddress.name}, ${this.order.userAddress.mobile}`,
          label: `${this.order.userAddress.province} ${this.order.userAddress.city} ${this.order.userAddress.country} ${this.order.userAddress.detail}`
        } : {
            title: '',
            label: ''
          }
      },
      id() {
        return this.$route.params.id
      },
      cart() {
        return this.$store.cart
      },
      productCounts() {
        let t = 0
        return this.orderProducts.reduce((prev, curr, index) => {
          return t += curr.count
        }, t)
      },
      total() {
        let t = 0
        return '￥' + this.orderProducts.reduce((prev, curr, index) => {
          return t += curr.count * curr.price
        }, t)
      },
      orderProducts() {
        if(Array.isArray(this.order.orderProducts)){
          return this.order.orderProducts
        }else{
          return []
        }
      }
    },
    watch: {
      payPassword: {
        handler(val) {
          if (val.length >= 6) {
            this.$axios.post(`/order/pay?order_id=${this.id}`, {
              pay_password: val
            }).then(res => {
              this.payPassword = ''
              if (res.errcode === 0) {
                this.$router.push({
                  name: 'paySuccess',
                  query: { orderId: this.id }
                })
              } else if (res.errcode === 901201) {
                this.$Toast(res.errmsg)
              } else {
                this.$router.push({
                  name: 'payFail',
                  query: { orderId: this.id }
                })
              }
            })
          }
        }
      }
    },
    methods: {
      onInput(key) {
        this.payPassword = (this.payPassword + key).slice(0, 6);
      },
      onDelete() {
        this.payPassword = this.payPassword.slice(0, this.payPassword.length - 1);
      }
    },
    mounted() {
      this.$axios.get(`/order?id=${this.id}`).then(res => {
        if (res.errcode === 0) {
          this.order = res.data
        }
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .bottom-bar {
    padding: 10px;
    display: flex;
    justify-content: space-between;
  }

  .bottom-bar .van-button {
    margin-left: 10px;
  }

  .bottom-bar .text {
    line-height: 30px;
  }

  .total {
    line-height: 36px;
  }

  .total span {
    color: #f44;
  }
</style>


